import React from "react";
import MoviesCardList from "../MoviesCardList/MoviesCardList";
import SearchForm from "../SearchForm/SearchForm";

import Pic1 from "../../images/pic-1.png";
import Pic2 from "../../images/pic-2.png";
import Pic3 from "../../images/pic-3.png";
import Pic4 from "../../images/pic-4.png";

import Header from "../Header/Header";
import Footer from "../Footer/Footer";

import "./Movies.css";

const data = [
  {
    id: 1,
    image: Pic1,
    nameRU: "33   ",
    duration: "1 42",
  },
  {
    id: 2,
    image: Pic2,
    nameRU: " 100  ",
    duration: "1 42",
  },
  {
    id: 3,
    image: Pic3,
    nameRU: "   ",
    duration: "1 42",
  },
  {
    id: 4,
    image: Pic4,
    nameRU: ":  ",
    duration: "1 42",
  },
  {
    id: 5,
    image: Pic1,
    nameRU: "33   ",
    duration: "1 42",
  },
  {
    id: 6,
    image: Pic2,
    nameRU: " 100  ",
    duration: "1 42",
  },
  {
    id: 7,
    image: Pic3,
    nameRU: "   ",
    duration: "1 42",
  },
  {
    id: 8,
    image: Pic4,
    nameRU: ":   :   :   :  ",
    duration: "1 42",
  }
]

function Movies(props) {
  return (
    <>
    <Header handleMenuClick={props.handleMenuClick}/>
      <section className="movies">
        <SearchForm />
        <MoviesCardList data={data}/>
        <button className="movies__button button"></button>
      </section>
    <Footer />
    </>
  )
}

export default Movies